.top-nav-container {
    display: block;
    min-height: 4em;
  }
  
  .navigation {
      display: -ms-flexbox;
      display: flex;
      background: #101010;
      color: white;
      top: 0;
      left: 0;
      right: auto;
      bottom: auto;
      position: fixed;
      width: 100%;
      height: 4em;
      margin: 0;
      z-index: 101;
  }

  .nav-title {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
      padding: 20px;
  }



